import { Component, Host, Prop, h } from '@stencil/core';
import i18next from 'i18next';
import { billingState, widgetState } from '../../store';
import { invoiceOptionArray } from '../../types/widget';
import { IWidgetState } from '../../store/types';

@Component({
  tag: 'charma-payment',
  styleUrl: 'charma-payment.scss',
  shadow: true,
})
export class CharmaPayment {
  // Passing this down so it will rerender once switched of language
  @Prop() language: string;

  handleBlur = (e: FocusEvent) => {
    const key = e.target['id'];
    if (widgetState.billingFormErrors?.[key]) {
      widgetState.billingFormErrors[key] = undefined;
      widgetState.error = undefined;
    }
  };

  handleInputChange = (e: CustomEvent) => {
    billingState[e.target['id']] = e.detail;
  };

  getError(id: keyof IWidgetState['billingFormErrors']): string {
    return widgetState.billingFormErrors?.[id]?.length > 0 ? widgetState.billingFormErrors?.[id][0] : '';
  }

  render() {
    return (
      <Host>
        <section>
          <div class="rounded-md px-6 pb-6 pt-3 mb-4 bg-white flex flex-col gap-3">
            <div class="grid grid-cols-1 md:grid-cols-2 gap-6 ">
              <charma-input
                required={true}
                label={i18next.t('billingInformation.form.organization_number')}
                name="organizationRegistrationId"
                onValueChange={this.handleInputChange}
                value={billingState.organizationRegistrationId}
                onBlur={this.handleBlur}
                error={this.getError('organizationRegistrationId')}
                id="organizationRegistrationId"
              ></charma-input>
              <charma-input
                required={true}
                onValueChange={this.handleInputChange}
                label={i18next.t('billingInformation.form.company_name')}
                name="organizationName"
                value={billingState.organizationName}
                error={this.getError('organizationName')}
                onBlur={this.handleBlur}
                id="organizationName"
              ></charma-input>
            </div>
            <div class="grid grid-cols-1 md:grid-cols-2 gap-6 ">
              <charma-dropdown
                options={invoiceOptionArray.map(opt => ({
                  label: opt,
                  value: opt,
                }))}
                required={true}
                defaultValue={{
                  value: billingState.invoiceType,
                  label: billingState.invoiceType,
                }}
                onValueChange={this.handleInputChange}
                label={i18next.t('billingInformation.form.invoice_delivery')}
                name="invoiceType"
                id="invoiceType"
                // error={this.getError('')}
              ></charma-dropdown>
              <charma-input
                value={billingState.email}
                onValueChange={this.handleInputChange}
                label={i18next.t('billingInformation.form.email')}
                name="email"
                id="email"
                error={this.getError('email')}
                onBlur={this.handleBlur}
                required
              ></charma-input>
            </div>
            <div class="grid grid-cols-1 md:grid-cols-2 gap-6 ">
              <charma-input
                required={true}
                value={billingState.postalCode}
                onValueChange={this.handleInputChange}
                label={i18next.t('billingInformation.form.zip')}
                name="postalCode"
                id="postalCode"
                error={this.getError('postalCode')}
                onBlur={this.handleBlur}
              ></charma-input>
              <charma-input
                required={true}
                value={billingState.city}
                onValueChange={this.handleInputChange}
                label={i18next.t('billingInformation.form.city')}
                name="city"
                id="city"
                error={this.getError('city')}
                onBlur={this.handleBlur}
              ></charma-input>
            </div>
            <div class="relative py-2 ">
              <charma-textarea
                value={billingState.message}
                onChange={(e: Event) => {
                  billingState.message = (e.target as HTMLTextAreaElement).value;
                }}
                label={i18next.t('billingInformation.form.message')}
                id="message"
              />
            </div>
          </div>
          {widgetState.hasError && <p class="text-red-400 text-sm font-body">*{i18next.t('errors.formError')}</p>}
        </section>
      </Host>
    );
  }
}
